.contacts {
  padding: 117px calc(50% - 620px) 120px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 40px;
  background-color: $color-default-blue;
  color: $color-default-white;

  @include vp-1319 {
    padding-left: 40px;
    padding-right: 40px;
  }

  @include vp-1199 {
    padding: 98px 45px 100px;
    grid-template-columns: 1fr;
    gap: 30px;
  }

  @include vp-767 {
    padding: 67px 15px;
    gap: 30px;
  }
}

.contacts__title {
  color: $color-default-white;
  padding: 5px 0;

  @include vp-1199 {
    padding: 0;
  }

  @include vp-767 {
    padding: 2px 0 0;
  }
}

.contacts__top {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding-bottom: 34px;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: rgba($color-default-white, 0.2);
  }

  @include vp-1199 {
    flex-direction: column;
    gap: 28px;
    padding-bottom: 28px;
  }

  @include vp-767 {
    gap: 30px;
    padding-bottom: 25px;
  }
}

.contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 48.7%;
  display: grid;
  grid-template-columns: 46.5% 24% 18%;
  gap: 38px;

  @include vp-1199 {
    grid-template-columns: 46.5% 26.7% 18%;
    gap: 40px;
    width: 100%;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }
}

.contacts__item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 12px;

  & p {
    margin: 0;
    padding: 0;
    line-height: 26px;
    max-width: 268px;
    display: flex;
    flex-direction: column;


    @include vp-1199 {
      line-height: 20px;
    }

    @include vp-767 {
      font-size: 14px;
      max-width: 100%;
    }

    a {
      color: $color-default-white;
      user-select: none;
      -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
      -webkit-tap-highlight-color: transparent;
    }

    a[href^="tel:"],
    a[href^="mailto:"] {
      @include hover-focus {
        position: relative;
        outline: none;

        &::after {
          content: "";
          position: absolute;
          width: 100%;
          height: 1px;
          left: 0;
          bottom: -5px;
          background-color: $color-default-white;
        }

        @include active {
          opacity: 0.8;

          &::after {
            display: none;
          }
        }
      }
    }
  }

  @include vp-1199 {
    gap: 15px;
  }

  @include vp-767 {
    gap: 6px;
  }
}

.contacts__key {
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 0.01em;
  opacity: 0.8;
}


.contacts__map {
  width: 100%;
  height: 500px;
  border-radius: 16px;
  overflow: hidden;

  @include vp-767 {
    height: 240px;
  }
}

.contacts__icon {
  width: 70px;
  height: 70px;

  @include vp-1199 {
    width: 40px;
    height: 40px;
  }

  @include vp-767 {
    width: 24px;
    height: 24px;
  }
}
